import React, { useEffect, useState } from 'react'
import TopBarSection from '../TopBar/TopBarSection'
import Axios from 'axios';
import { Link } from 'react-router-dom';
import { ACTIONS, Create, Delete, Id, ModulePermission, Read, Role, STATUS, Update } from '../../Components/TableHeading/TableHeading'

const Addadmin = () => {

    const [Permissions, setPermissions] = useState([])
    useEffect(() => {
        fetch('http://localhost:3001/Permissions').then((res) => {
            console.log(res)
            return res.json()
        })
            .then((res) => {
                console.log('Success', res)
                setPermissions(res)
                return res
            })
            .catch((err) => {
                console.log(err)
            })
    }, [])

    const [Roles, setRoles] = useState([])
    useEffect(() => {
        fetch('http://localhost:3001/Roles').then((res) => {
            console.log(res)
            return res.json()
        })
            .then((res) => {
                console.log('Success', res)
                setRoles(res)
                return res
            })
            .catch((err) => {
                console.log(err)
            })
    }, [])

    const [admin_fname, setAdmin_fname] = useState('')
    const [admin_lname, setAdmin_lname] = useState('')
    const [admin_username, setAdmin_username] = useState('')
    const [role, setRole] = useState('')
    const [admin_password, setAdmin_password] = useState('')
    const [admin_phone, setAdmin_phone] = useState('')
    const [admin_email, setAdmin_email] = useState('')
    const [admin_desc, setAdmin_desc] = useState('')
    const [admin_add, setAdmin_add] = useState('')
    const [admin_country, setAdmin_country] = useState('')
    const [admin_state, setAdmin_state] = useState('')
    const [admin_city, setAdmin_city] = useState('')

    const submitAdminData = () => {
        Axios.post('http://localhost:3001/api/insertAdmin', {
            admin_fname: admin_fname,
            admin_lname: admin_lname,
            admin_username: admin_username,
            role: role,
            admin_password: admin_password,
            admin_phone: admin_phone,
            admin_email: admin_email,
            admin_desc: admin_desc,
            admin_add: admin_add,
            admin_country: admin_country,
            admin_state: admin_state,
            admin_city: admin_city,
        });
    }
    return (
        <div className="content">
            <TopBarSection />

            <div class="modal-content">
                <div class="modal-header">
                    <h2 class="font-medium text-base mr-auto">
                        Add New
                    </h2>
                </div>
                <div class="modal-body grid grid-cols-12 gap-4 gap-y-3">
                    <div class="col-span-6">
                        <label for="pos-form-1" class="form-label">First Name</label>
                        <input id="pos-form-1" type="text" name="admin_fname" class="form-control flex-1" placeholder="name" onChange={(e) => {
                            setAdmin_fname(e.target.value)
                        }} required />
                    </div>
                    <div class="col-span-6">
                        <label for="pos-form-2" class="form-label">Last Name</label>
                        <input id="pos-form-2" type="text" name="admin_lname" class="form-control flex-1" placeholder="Phone" onChange={(e) => {
                            setAdmin_lname(e.target.value)
                        }} required />
                    </div>
                    <div class="col-span-6">
                        <label for="pos-form-2" class="form-label">Username</label>
                        <input id="pos-form-2" type="text" name="admin_username" class="form-control flex-1" placeholder="Username" onChange={(e) => {
                            setAdmin_username(e.target.value)
                        }} required />
                    </div>
                    <div class="col-span-6">
                        <label for="pos-form-2" class="form-label">Role</label>
                        <select name="role" id="cars" class="form-control flex-1" onChange={(e) => {
                            setRole(e.target.value)
                        }}>
                            {
                                Roles.map(Role => {
                                    return (
                                        <option class="form-control flex-1" value={Role.role_name}>{Role.role_name}</option>
                                    )
                                })
                            }
                        </select>
                    </div>
                    <div class="col-span-6">
                        <label for="pos-form-2" class="form-label">Password</label>
                        <input id="pos-form-2" type="text" name="admin_password" class="form-control flex-1" placeholder="Password" onChange={(e) => {
                            setAdmin_password(e.target.value)
                        }} required />
                    </div>
                    <div class="col-span-6">
                        <label for="pos-form-2" class="form-label">Phone</label>
                        <input id="pos-form-2" type="text" name="admin_phone" class="form-control flex-1" placeholder="admin_phone" onChange={(e) => {
                            setAdmin_phone(e.target.value)
                        }} required />
                    </div>
                    <div class="col-span-6">
                        <label for="pos-form-2" class="form-label">Email</label>
                        <input id="pos-form-2" type="email" name="admin_email" class="form-control flex-1" placeholder="admin_email" onChange={(e) => {
                            setAdmin_email(e.target.value)
                        }} required />
                    </div>
                    <div class="col-span-6">
                        <label for="pos-form-2" class="form-label">Desc</label>
                        <input id="pos-form-2" type="text" name="admin_desc" class="form-control flex-1" placeholder="admin_desc" onChange={(e) => {
                            setAdmin_desc(e.target.value)
                        }} required />
                    </div>
                    <div class="col-span-6">
                        <label for="pos-form-2" class="form-label">Address</label>
                        <input id="pos-form-2" type="text" name="admin_add" class="form-control flex-1" placeholder="admin_add" onChange={(e) => {
                            setAdmin_add(e.target.value)
                        }} required />
                    </div>
                    <div class="col-span-6">
                        <label for="pos-form-2" class="form-label">Country</label>
                        <input id="pos-form-2" type="text" name="admin_country" class="form-control flex-1" placeholder="admin_country" onChange={(e) => {
                            setAdmin_country(e.target.value)
                        }} required />
                    </div>
                    <div class="col-span-6">
                        <label for="pos-form-2" class="form-label">State</label>
                        <input id="pos-form-2" type="text" name="admin_state" class="form-control flex-1" placeholder="admin_state" onChange={(e) => {
                            setAdmin_state(e.target.value)
                        }} required />
                    </div>
                    <div class="col-span-6">
                        <label for="pos-form-2" class="form-label">City</label>
                        <input id="pos-form-2" type="text" name="admin_city" class="form-control flex-1" required placeholder="admin_city" onChange={(e) => {
                            setAdmin_city(e.target.value)
                        }} />
                    </div>
                </div>
                {/* <div class="modal-body grid grid-cols-6 gap-4 gap-y-3"> */}
                <table className="table table-report sm:mt-2">
                    <thead>
                        <tr>
                            {/* <Id /> */}
                            <ModulePermission />
                            <Create />
                            <Read />
                            <Update />
                            <Delete />
                        </tr>
                    </thead>

                    <tbody>
                        {
                            Permissions.map(Permission => {
                                return (
                                    <tr className="intro-x">
                                        {/* <td className="text-center">{Permission.id}</td> */}
                                        <td className="text-center permission">{Permission.permissions_name}</td>
                                        <td className="text-center">
                                            <label class="switch">
                                                <input type="checkbox" />
                                                <span class="slider round"></span>
                                            </label>
                                        </td>
                                        <td className="text-center">  <label class="switch">
                                            <input type="checkbox" />
                                            <span class="slider round"></span>
                                        </label></td>
                                        <td className="text-center">  <label class="switch">
                                            <input type="checkbox" />
                                            <span class="slider round"></span>
                                        </label></td>
                                        <td className="text-center">  <label class="switch">
                                            <input type="checkbox" />
                                            <span class="slider round"></span>
                                        </label></td>

                                    </tr>
                                )
                            })
                        }
                    </tbody>
                </table>
                {/* </div> */}

                <div class="modal-footer text-right">
                    <Link to="/roles" data-dismiss="modal" class="btn btn-outline-secondary w-32 mr-1">Cancel</Link>
                    <Link to="/roles"><button class="btn btn-primary w-32" onClick={submitAdminData}>Add Admin</button></Link>
                </div>
            </div>
        </div>
    )
}

export default Addadmin
