import React,{useState, useEffect} from "react";
import TopBarSection from "../../TopBar/TopBarSection";
import { Link } from "react-router-dom";
import { DriverVehh } from "../../../Components/Heading.js/Heading";
import {
  ACTIONS,
  Brand,
  Color,
  Country,
  Driver,
  Id,
  STATUS,
  VehicleName,
  VehicleNumber,
  VehicleType,
} from "../../../Components/TableHeading/TableHeading";

const DriverVeh = () => {
  const [driverVehicles, setDriverVehicles] = useState([]);
  useEffect(() => {
    fetch("http://localhost:3001/drivervehicles")
      .then((res) => {
        console.log(res);
        return res.json();
      })
      .then((res) => {
        console.log("Success", res);
        setDriverVehicles(res);
        return res;
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);
  return (
    <div className="content">
      <TopBarSection />

      <div className="col-span-12 mt-6">
        <div className="intro-y block sm:flex items-center h-10">
          <DriverVehh />
          <div className="flex items-center sm:ml-auto mt-3 sm:mt-0">
          <Link class="btn btn-primary shadow-md mr-2" to="/add-driver-veh">Add New</Link>
            <button className="btn box flex items-center text-gray-700 dark:text-gray-300">
              
              <i
                data-feather="file-text"
                className="hidden sm:block w-4 h-4 mr-2"
              ></i>
              Export to Excel
            </button>
            <button className="ml-3 btn box flex items-center text-gray-700 dark:text-gray-300">
              
              <i
                data-feather="file-text"
                className="hidden sm:block w-4 h-4 mr-2"
              ></i>
              Export to PDF
            </button>
          </div>
        </div>

        <div className="intro-y overflow-auto lg:overflow-visible mt-8 sm:mt-0">
          <table className="table table-report sm:mt-2">
            <thead>
              <tr>
                <Id />
                <Country />
                <Driver />
                <VehicleType />
                <Brand />
                <Color />
                <VehicleName />
                <VehicleNumber />
                <STATUS />
                <ACTIONS />
              </tr>
            </thead>
            <tbody>
              {driverVehicles.map((vehicles) => {
                return (
                  <tr className="intro-x">
                    <td className="text-center">{vehicles.drivervehicle_id }</td>
                    <td className="text-center">{vehicles.country}</td>
                    <td className="text-center">{vehicles.driver_name}</td>
                    <td className="text-center">{vehicles.vehicle_type}</td>
                    <td className="text-center">{vehicles.brand}</td>
                    <td className="text-center">{vehicles.color}</td>
                    <td className="text-center">{vehicles.vehicle_name}</td>
                    <td className="text-center">{vehicles.vehicle_number}</td>
                    <td className="w-40">
                      <div className="flex items-center justify-center text-theme-9">
                        
                        <i
                          data-feather="check-square"
                          className="w-4 h-4 mr-2"
                        ></i>
                        Active
                      </div>
                    </td>
                    <td className="table-report__action w-56">
                      <div className="flex justify-center items-center">
                        <a className="flex items-center mr-3" href="">
                          <i
                            data-feather="check-square"
                            className="w-4 h-4 mr-1"
                          ></i>
                          Edit
                        </a>
                        <a className="flex items-center text-theme-6" href="">
                          
                          <i
                            data-feather="trash-2"
                            className="w-4 h-4 mr-1"
                          ></i>
                          Delete
                        </a>
                      </div>
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
};

export default DriverVeh;
