import React, { useState } from "react";
import TopBarSection from "../../TopBar/TopBarSection";
import { Link } from "react-router-dom";
import Axios from "axios";

const DailyFareAdd = () => {
    const [country, setcountry] = useState("");
    const [vehicleType, setvehicleType] = useState("");
    const [baseFare, setbaseFare] = useState("");
    const [baseFarePerKm, setbaseFarePerKm] = useState("");
    const [pricePerKm, setpricePerKm] = useState("");
    const [driverAllowance, setdriverAllowance] = useState("");
    console.log(baseFare, baseFarePerKm, pricePerKm, driverAllowance);
    
  
    const submitData = () => {
      Axios.post("http://localhost:3001/api/outstatiomfaremana", {
          country: country,
          vehicle_types: vehicleType,
          base_fare: baseFare,
          BaseFareCoverKm: baseFarePerKm,
          PricePerKm: pricePerKm,
          DriverAllowance: driverAllowance,

      });
      console.log(submitData);
    };

  return (
    <div className="content">
      <TopBarSection />

      <div class="modal-content">
        <div class="modal-header">
          <h2 class="font-medium text-base mr-auto">Add New</h2>
        </div>
        <div class="modal-body grid grid-cols-12 gap-4 gap-y-3">
        <div class="col-span-6">
            <label for="pos-form-1" class="form-label">
              Country
            </label>
            <input
              id="pos-form-1"
              type="text"
              name="country"
              class="form-control flex-1"
              placeholder="Country"
              onChange={(e) => {
                setcountry(e.target.value);
              }}
            />
          </div>
          <div class="col-span-6">
            <label for="pos-form-2" class="form-label">
              Vehicle Type
            </label>
            <input
              id="pos-form-2"
              type="text"
              name="vehicle_types"
              class="form-control flex-1"
              placeholder="Vehicle Type"
              onChange={(e) => {
                setvehicleType(e.target.value);
              }}
            />
          </div>
          <div class="col-span-6">
            <label for="pos-form-2" class="form-label">
            Base Fare 
            </label>
            <input
              id="pos-form-2"
              type="text"
              name="base_fare"
              class="form-control flex-1"
              placeholder="Base Fare"
              onChange={(e) => {
                setbaseFare(e.target.value);
              }}
            />
          </div>

          <div class="col-span-6">
            <label for="pos-form-2" class="form-label">
            Base Fare Cover Km
            </label>
            <input
              id="pos-form-2"
              type="text"
              name="BaseFareCoverKm"
              class="form-control flex-1"
              placeholder="Base Fare Cover Km"
              onChange={(e) => {
                setbaseFarePerKm(e.target.value);
              }}
            />
          </div>
          <div class="col-span-6">
            <label for="pos-form-2" class="form-label">
              Price Per Km
            </label>
            <input
              id="pos-form-2"
              type="text"
              name="PricePerKm"
              class="form-control flex-1"
              placeholder="Price Per Km"
              onChange={(e) => {
                setpricePerKm(e.target.value);
              }}
            />
          </div>
          <div class="col-span-6">
            <label for="pos-form-2" class="form-label">
              Driver Allowance
            </label>
            <input
              id="pos-form-2"
              type="text"
              name="DriverAllowance"
              class="form-control flex-1"
              placeholder="Driver Allowance"
              onChange={(e) => {
                setdriverAllowance(e.target.value);
              }}
            />
          </div>
          
          </div>
        </div>

        <div class="modal-footer text-right">
          <Link
            to=""
            data-dismiss="modal"
            class="btn btn-outline-secondary w-32 mr-1"
          >
            Cancel
          </Link>
          
            <Link to="/Outstation-Fare-Mana" class="btn btn-primary w-32" onClick={submitData}>Add</Link>
          
        </div>
      </div>
  );
};

export default DailyFareAdd;
