import React, { useState } from 'react'
import TopBarSection from '../../TopBar/TopBarSection'
import Axios from 'axios';


const RentalBooking = () => {
    const [admin_select, setAdmin_select] = useState('')
    const [admin_enterpho, setAdmin_enterpho] = useState('')
    const [admin_getdeatails, setAdmin_getdetails] = useState('')
    const [admin_username, setAdmin_username] = useState('')
    const [admin_email, setAdmin_email] = useState('')
    const [admin_selectcity, setAdmin_selectcity] = useState('')
    const [admin_selectpackege, setAdmin_selectpackege] = useState('')
    const [admin_ridenow, setAdmin_ridenow] = useState('')
    const [admin_ridelater, setAdmin_ridelater] = useState('')
    const [admin_selectdate, setAdmin_selectdate] = useState('')
    const [admin_selecttime, setAdmin_selecttime] = useState('')
    const [admin_pickuplocation, setAdmin_pickuplocation] = useState('')
    const [admin_selectcartype, setAdmin_selectcartype] = useState('')
    const [admin_radius, setAdmin_radius] = useState('')
    const [admin_autoassigndriver, setAdmin_autoassigndriver] = useState('')
    const [admin_selectdriver, setAdmin_selectdriver] = useState('')
    const [admin_applycoupan, setAdmin_applycoupan] = useState('')
    const [admin_selectpaymentmethod, setAdmin_selectpaymentmethod] = useState('')
 

    const submitAdminData = () => {
        Axios.post('http://localhost:3001/api/insertAdmin', {
            admin_select: admin_select,
            admin_enterpho: admin_enterpho,
            admin_getdeatails: admin_getdeatails,
            admin_username: admin_username,
            admin_email: admin_email,
            admin_selectcity: admin_selectcity,
            admin_selectpackege: admin_selectpackege,
            admin_ridenow: admin_ridenow,
            admin_ridelater: admin_ridelater,
            admin_selectdate: admin_selectdate,
            admin_selecttime: admin_selecttime,
            admin_pickuplocation: admin_pickuplocation,
            admin_droplocation: admin_selectcartype,
            admin_radius: admin_radius,
            admin_autoassigndriver: admin_autoassigndriver,
            admin_selectdriver: admin_selectdriver,
            admin_applycoupan: admin_applycoupan,
            admin_selectpaymentmethod: admin_selectpaymentmethod,
           
        });
    }

    return (
        <div className="content">
            <TopBarSection />

            <div class="modal-content">
                <div class="modal-header">
                    <h2 class="font-medium text-base mr-auto">
                        Add New
                    </h2>
                </div>

                <div class="modal-body grid grid-cols-12 gap-4 gap-y-3">
                    <div class="col-span-6">
                        <label for="pos-form-1" class="form-label">Select</label>
                        <input id="pos-form-1" type="text" name="admin_select" class="form-control flex-1" placeholder="select" onChange={(e) => {
                            setAdmin_select(e.target.value)
                        }} />
                    </div>
                    <div class="col-span-6">
                        <label for="pos-form-2" class="form-label">Enter phone</label>
                        <input id="pos-form-2" type="text" name="admin_enterpho" class="form-control flex-1" placeholder="enterpho" onChange={(e) => {
                            setAdmin_enterpho(e.target.value)
                        }} />
                    </div>
                    <div class="col-span-6">
                        <label for="pos-form-2" class="form-label">Get details</label>
                        <input id="pos-form-2" type="text" name="admin_getdetails" class="form-control flex-1" placeholder="Get_details" onChange={(e) => {
                            setAdmin_getdetails(e.target.value)
                        }} />
                    </div>
                    <div class="col-span-6">
                        <label for="pos-form-2" class="form-label">Username</label>
                        <input id="pos-form-2" type="text" name="admin_username" class="form-control flex-1" placeholder="admin_username" onChange={(e) => {
                            setAdmin_username(e.target.value)
                        }} />
                    </div>
                    <div class="col-span-6">
                        <label for="pos-form-2" class="form-label">Email</label>
                        <input id="pos-form-2" type="email" name="admin_email" class="form-control flex-1" placeholder="admin_email" onChange={(e) => {
                            setAdmin_email(e.target.value)
                        }} />
                    </div>
                    <div class="col-span-6">
                        <label for="pos-form-2" class="form-label">Select city</label>
                        <input id="pos-form-2" type="text" name="admin_selectcity" class="form-control flex-1" placeholder="admin_selectcity" onChange={(e) => {
                            setAdmin_selectcity(e.target.value)
                        }} />
                    </div>
                    <div class="col-span-6">
                        <label for="pos-form-2" class="form-label">Select packege</label>
                        <input id="pos-form-2" type="selectpackege" name="admin_selectpackege" class="form-control flex-1" placeholder="admin_selectpackege" onChange={(e) => {
                            setAdmin_selectpackege(e.target.value)
                        }} />
                    </div>
                    <div class="col-span-6">
                        <label for="pos-form-2" class="form-label">RideNow</label>
                        <checkbox id="pos-form-2" type="text" name="admin_desc" class="form-control flex-1" placeholder="admin_ridenow" onChange={(e) => {
                            setAdmin_ridenow(e.target.value)
                        }} />
                    </div>
                    <div class="col-span-6">
                        <label for="pos-form-2" class="form-label">RideLater</label>
                        <checkbox id="pos-form-2" type="text" name="admin_desc" class="form-control flex-1" placeholder="admin_ridelater" onChange={(e) => {
                            setAdmin_ridelater(e.target.value)
                        }} />
                    </div>
                    <div class="col-span-6">
                        <label for="pos-form-2" class="form-label">Select Date</label>
                        <input id="pos-form-2" type="text" name="admin_selectdate" class="form-control flex-1" placeholder="admin_selectdate" onChange={(e) => {
                            setAdmin_selectdate(e.target.value)
                        }} />
                    </div>
                    <div class="col-span-6">
                        <label for="pos-form-2" class="form-label">Select time</label>
                        <input id="pos-form-2" type="text" name="admin_selecttime" class="form-control flex-1" placeholder="admin_selecttime" onChange={(e) => {
                            setAdmin_selecttime(e.target.value)
                        }} />
                    </div>
                    <div class="col-span-6">
                        <label for="pos-form-2" class="form-label">PickUpLocation</label>
                        <input id="pos-form-2" type="text" name="admin_pickuplocation" class="form-control flex-1" placeholder="admin_pickuplocation" onChange={(e) => {
                            setAdmin_pickuplocation(e.target.value)
                        }} />
                    </div>
                    <div class="col-span-6">
                        <label for="pos-form-2" class="form-label">Select CarType</label>
                        <select id="pos-form-2" type="text" name="admin_selectcartype" class="form-control flex-1" placeholder="admin_selectcartype" onChange={(e) => {
                            setAdmin_selectcartype(e.target.value)
                        }} />
                    </div>

                    <div class="col-span-6">
                        <label for="pos-form-2" class="form-label">2km Radius</label>
                        <select id="pos-form-2" type="text" name="admin_radius" class="form-control flex-1" placeholder="admin_radius" onChange={(e) => {
                            setAdmin_radius(e.target.value)
                        }} />
                    </div>
                    <div class="col-span-6">
                        <label for="pos-form-2" class="form-label">Send to all driver</label>
                        <checkbox id="pos-form-2" type="text" name="admin_autoassigndriver" class="form-control flex-1" placeholder="admin_autoassigndriver" onChange={(e) => {
                            setAdmin_autoassigndriver(e.target.value)
                        }} />
                    </div>

                    <br />
                    <h2>OR</h2>

                    <div class="col-span-6">
                        <label for="pos-form-2" class="form-label">Select Driver</label>
                        <input id="pos-form-2" type="text" name="admin_selectdriver" class="form-control flex-1" placeholder="admin_selectdriver" onChange={(e) => {
                            setAdmin_selectdriver(e.target.value)
                        }} />
                    </div>

                    <div class="col-span-6">
                        <label for="pos-form-2" class="form-label">Apply Coupan</label>
                        <input id="pos-form-2" type="text" name="admin_applycoupan" class="form-control flex-1" placeholder="admin_applycoupan" onChange={(e) => {
                            setAdmin_applycoupan(e.target.value)
                        }} />
                    </div>
                    <div class="col-span-6">
                        <label for="pos-form-2" class="form-label">Select PaymentMethod</label>
                        <input id="pos-form-2" type="text" name="admin_selectpaymentmethod" class="form-control flex-1" placeholder="admin_selectpaymentmethod" onChange={(e) => {
                            setAdmin_selectpaymentmethod(e.target.value)
                        }} />
                    </div>

                    <div class="modal-footer text-right">
                        <button class="btn btn-primary w-32" onClick={submitAdminData}>Book Now</button>
                    </div>
                </div>



            </div>
        </div>
    )
}

export default RentalBooking
