import React, { useState } from 'react'
import Axios from 'axios';
import { Link } from 'react-router-dom';
import TopBarSection from '../../TopBar/TopBarSection';

const AddContact = () => {
    const [contact_number, setContact_number] = useState('')
    const [contact_email, setContact_email] = useState('')
    const [contact_address, setContact_address] = useState('')

    const submitContactSettingData = () => {
        Axios.post('http://localhost:3001/api/insetContactSetting', {
            contact_number: contact_number,
            contact_email: contact_email,
            contact_address: contact_address,
        });
    }
    return (
        <div className="content">
            <TopBarSection />

            <div class="modal-content">
                <div class="modal-header">
                    <h2 class="font-medium text-base mr-auto">
                        Add New Contact
                    </h2>
                </div>
                <div class="modal-body grid grid-cols-12 gap-4 gap-y-3">
                    <div class="col-span-12">
                        <label for="pos-form-1" class="form-label">Phone Number</label>
                        <input id="pos-form-1" type="phone" name="contact_number" class="form-control flex-1" placeholder="contact number" onChange={(e) => {
                            setContact_number(e.target.value)
                        }} />
                    </div>
                    <div class="col-span-12">
                        <label for="pos-form-1" class="form-label">Email</label>
                        <input id="pos-form-1" type="email" name="contact_email" class="form-control flex-1" placeholder="Email" onChange={(e) => {
                            setContact_email(e.target.value)
                        }} />
                    </div>
                    <div class="col-span-12">
                        <label for="pos-form-1" class="form-label">Address</label>
                        <input id="pos-form-1" type="text" name="contact_address" class="form-control flex-1" placeholder="Address" onChange={(e) => {
                            setContact_address(e.target.value)
                        }} />
                    </div>
                </div>
                <div class="modal-footer text-right">
                    <Link to="/All-Roles" type="button" data-dismiss="modal" class="btn btn-outline-secondary w-32 mr-1">Cancel</Link>
                    <Link to="/Contact-Setting" class="btn btn-primary w-32" onClick={submitContactSettingData}>Add Role</Link>
                </div>
            </div>
        </div>
    )
}

export default AddContact
