import React, { useEffect, useState } from 'react'
import TopBarSection from '../../TopBar/TopBarSection'
import {  Country, Email, FirstName, Id, LastName, Phone, STATUS, Gender, Currency, ACTIONS, Address } from '../../../Components/TableHeading/TableHeading'
import { Link } from 'react-router-dom'
import Axios from 'axios'

const ContactSetting = () => {
    const [contacts, setContacts] = useState([])
    useEffect(() => {
        fetch('http://localhost:3001/contactSetting').then((res) => {
            console.log(res)
            return res.json()
        })
            .then((res) => {
                console.log('Success', res)
                setContacts(res)
                return res
            })
            .catch((err) => {
                console.log(err)
            })
    }, [])

    const deletecontactsetting = (contact_id) => {
        Axios.delete(`http://localhost:3001/contactSetting/${contact_id}`).then((response) => {
            setContacts(
                contacts.filter((val) => {
                    return val.contact_id != contact_id;
                })
            );
        });
    };
    return (
        <div className="content">

            <TopBarSection />

            <div className="col-span-12 mt-6">
                <div className="intro-y block sm:flex items-center h-10">
                <h2 class="text-lg font-medium truncate mr-5">Contact Setting <small>list</small></h2>
                    <div className="flex items-center sm:ml-auto mt-3 sm:mt-0">
                        <Link class="btn btn-primary shadow-md mr-2" to="/Add-Contact">Add New</Link>
                        <button className="btn box flex items-center text-gray-700 dark:text-gray-300"> <i data-feather="file-text" className="hidden sm:block w-4 h-4 mr-2"></i> Export to Excel </button>
                        <button className="ml-3 btn box flex items-center text-gray-700 dark:text-gray-300"> <i data-feather="file-text" className="hidden sm:block w-4 h-4 mr-2"></i> Export to PDF </button>
                    </div>
                </div>
                <div className="intro-y overflow-auto lg:overflow-visible mt-8 sm:mt-0">
                    <table className="table table-report sm:mt-2">
                        <thead>
                            <tr>
                                <Id />
                                <Phone />
                                <Email />
                                <Address />
                                <STATUS />
                                <ACTIONS />
                            </tr>
                        </thead>
                        <tbody>
                        {
                                contacts.map(contact => {
                                    return (
                                        <tr className="intro-x">
                                            <td className="text-center">{contact.contact_id}</td>
                                            <td className="text-center">{contact.contact_number}</td>
                                            <td className="text-center">{contact.contact_email}</td>
                                            <td className="text-center">{contact.contact_address}</td>
                                            <td className="w-40">
                                                <div className="flex items-center justify-center text-theme-9"> <i data-feather="check-square" className="w-4 h-4 mr-2"></i> Active </div>
                                            </td>
                                            <td className="table-report__action w-56">
                                                <div className="flex justify-center items-center">
                                                    <Link className="flex items-center mr-3" to="/define-Roles"> <i data-feather="check-square" className="w-4 h-4 mr-1"></i> Edit </Link>

                                                    <button className="flex items-center text-theme-6" onClick={() => {
                                                        deletecontactsetting(contact.contact_id);
                                                    }}> <i data-feather="trash-2" className="w-4 h-4 mr-1"></i> Delete </button>
                                                </div>
                                            </td>
                                        </tr>
                                    )
                                })
                            }
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    )
}

export default ContactSetting
