import React from 'react'
import { auth } from "../../../firebase";

const Logout = () => {
    const logOutFUnction = async () => {
        try {
            await auth.signOut();
            localStorage.removeItem("tokenOtp");
            localStorage.removeItem("loggedInOtp");
            localStorage.removeItem("loggedIn");
            window.location.reload(false);
        } catch (error) {
            console.log("error signing out: ", error);
        }
    };
    return (
        <div className="p-2 border-t border-theme-27 dark:border-dark-3">
            <button onClick={logOutFUnction} className="flex items-center block p-2 transition duration-300 ease-in-out hover:bg-theme-1 dark:hover:bg-dark-3 rounded-md"> <i data-feather="toggle-right" className="w-4 h-4 mr-2"></i> Logout </button>
        </div>
    )
}

export default Logout
