import React, {useState, useEffect}from 'react'
import { OutstationFare } from '../../../Components/Heading.js/Heading'
import { ACTIONS, BaseFare, BaseFareCoverKm, Country, DriverAllowance, Id, PricePerKm, STATUS, VehicleType } from '../../../Components/TableHeading/TableHeading'
import TopBarSection from '../../TopBar/TopBarSection'
import { Link } from 'react-router-dom'

const OutstationFareMana = () => {
    const [outstation, setoutstation] = useState([])
    useEffect(() => {
        fetch('http://localhost:3001/outstationfaremana').then((res) => {
            console.log(res)
            return res.json()
        })
            .then((res) => {
                console.log('Success', res)
                setoutstation(res)
                return res
            })
            .catch((err) => {
                console.log(err)
            })
    }, [])
    return (
        <div className="content">
            <TopBarSection />

            <div className="col-span-12 mt-6">
                <div className="intro-y block sm:flex items-center h-10">
                    <OutstationFare />
                    <div className="flex items-center sm:ml-auto mt-3 sm:mt-0">
                        <Link class="btn btn-primary shadow-md mr-2" to="/add-outstanding-fare-mana">Add New</Link>
                        <button className="btn box flex items-center text-gray-700 dark:text-gray-300"> <i data-feather="file-text" className="hidden sm:block w-4 h-4 mr-2"></i> Export to Excel </button>
                        <button className="ml-3 btn box flex items-center text-gray-700 dark:text-gray-300"> <i data-feather="file-text" className="hidden sm:block w-4 h-4 mr-2"></i> Export to PDF </button>
                    </div>
                </div>

                <div className="intro-y overflow-auto lg:overflow-visible mt-8 sm:mt-0">
                    <table className="table table-report sm:mt-2">
                        <thead>
                            <tr>
                                <Id />
                                <Country />
                                <VehicleType />
                                <BaseFare />
                                <BaseFareCoverKm />
                                <PricePerKm />
                                <DriverAllowance />
                                <STATUS />
                                <ACTIONS />
                            </tr>
                        </thead>
                        <tbody>
                        {
                                outstation.map(outstationmana => {
                                    return (
                                        <tr className="intro-x">
                                            <td className="text-center">{outstationmana.outstation_fare_mana_id}</td>
                                            <td className="text-center">{outstationmana.country}</td>
                                            <td className="text-center">{outstationmana.vehicle_types}</td>
                                           
                                            <td className="text-center">{outstationmana.base_fare}</td>
                                            <td className="text-center">{outstationmana.BaseFareCoverKm}</td>
                                            <td className="text-center">{outstationmana.PricePerKm}</td>
                                            <td className="text-center">{outstationmana.DriverAllowance}</td>
                                            <td className="text-center">{outstationmana.status}</td>
                                            <td className="w-40">
                                                <div className="flex items-center justify-center text-theme-9"> <i data-feather="check-square" className="w-4 h-4 mr-2"></i> Active </div>
                                            </td>
                                            <td className="table-report__action w-56">
                                                <div className="flex justify-center items-center">
                                                    <a className="flex items-center mr-3" href=""> <i data-feather="check-square" className="w-4 h-4 mr-1"></i> Edit </a>
                                                    <a className="flex items-center text-theme-6" href=""> <i data-feather="trash-2" className="w-4 h-4 mr-1"></i> Delete </a>
                                                </div>
                                            </td>
                                        </tr>
                                    )
                                })
                            }
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    )
}

export default OutstationFareMana
