import React, { useEffect, useState } from 'react';
// import Heading from '../../Components/Heading';
import { ACTIONS, Title, UserType, Id, STATUS, Country } from '../../Components/TableHeading/TableHeading';
import TopBarSection from '../TopBar/TopBarSection';


const PrivacyPolicies = () => {
    const [privacy_policies, setprivacy_policies] = useState([])
    useEffect(() => {
        fetch('http://localhost:3001/privacy_policies').then((res) => {
            console.log(res)
            return res.json()
        })
            .then((res) => {
                console.log('Success', res)
                setprivacy_policies(res)
                return res
            })
            .catch((err) => {
                console.log(err)
            })
    }, [])
    return (
        <div className="content">

            <TopBarSection />

            <div className="col-span-12 mt-6">
                <div className="intro-y block sm:flex items-center h-10">
                    {/* <Heading heading="Privacy Policies" /> */}
                    <div className="flex items-center sm:ml-auto mt-3 sm:mt-0">
                        <button class="btn btn-primary shadow-md mr-2">Add New</button>
                        <button className="btn box flex items-center text-gray-700 dark:text-gray-300"> <i data-feather="file-text" className="hidden sm:block w-4 h-4 mr-2"></i> Export to Excel </button>
                        <button className="ml-3 btn box flex items-center text-gray-700 dark:text-gray-300"> <i data-feather="file-text" className="hidden sm:block w-4 h-4 mr-2"></i> Export to PDF </button>
                    </div>
                </div>
                <div className="intro-y overflow-auto lg:overflow-visible mt-8 sm:mt-0">
                    <table className="table table-report sm:mt-2">
                        <thead>
                            <tr>
                                <Id />
                                <Country />
                                <UserType />
                                <Title />
                                <STATUS />
                                <ACTIONS />
                            </tr>
                        </thead>

                        <tbody>
                            {
                                privacy_policies.map(Privacy_policy => {
                                    return (
                                        <tr className="intro-x">
                                            <td className="text-center">{Privacy_policy.privacy_policy_id }</td>
                                            <td className="text-center">{Privacy_policy.country}</td>
                                            <td className="text-center">{Privacy_policy.user_type}</td>
                                            <td className="text-center">{Privacy_policy.title}</td>
                                            <td className="text-center">{Privacy_policy.status}</td>
                                            <td className="text-center">{Privacy_policy.date}</td>
                                            <td className="w-40">
                                                <div className="flex items-center justify-center text-theme-9"> <i data-feather="check-square" className="w-4 h-4 mr-2"></i> Active </div>
                                            </td>
                                            <td className="table-report__action w-56">
                                                <div className="flex justify-center items-center">
                                                    <a className="flex items-center mr-3" href=""> <i data-feather="check-square" className="w-4 h-4 mr-1"></i> Edit </a>
                                                    <a className="flex items-center text-theme-6" href=""> <i data-feather="trash-2" className="w-4 h-4 mr-1"></i> Delete </a>
                                                </div>
                                            </td>
                                        </tr>
                                    )
                                })
                            }
                        </tbody>
                    </table>
                </div>

            </div>
        </div>
    )
}

export default PrivacyPolicies;
