import React, { useState } from "react";
import TopBarSection from "../../TopBar/TopBarSection";
import { Link } from "react-router-dom";
import Axios from "axios";

const DailyFareAdd = () => {
  const [packageName, setpackageName] = useState("");
  const [hours, sethours] = useState("");
  const [killoMetre, setkilloMetre] = useState("");
  

  const submitData = () => {
    Axios.post("http://localhost:3001/api/insertpackages", {
        packages_name: packageName,
        hours: hours,
        kilometers: killoMetre,
    });
    console.log(submitData);
  };

  return (
    <div className="content">
      <TopBarSection />

      <div class="modal-content">
        <div class="modal-header">
          <h2 class="font-medium text-base mr-auto">Add New</h2>
        </div>
        <div class="modal-body grid grid-cols-12 gap-4 gap-y-3">
          <div class="col-span-4">
            <label for="pos-form-1" class="form-label">
              Package Name
            </label>
            <input
              id="pos-form-1"
              type="text"
              name="vehicles_type"
              class="form-control flex-1"
              placeholder="Package Name"
              onChange={(e) => {
                setpackageName(e.target.value);
              }}
            />
          </div>
          <div class="col-span-4">
            <label for="pos-form-2" class="form-label">
              Hours
            </label>
            <input
              id="pos-form-2"
              type="text"
              name="starting_km"
              class="form-control flex-1"
              placeholder="Hours"
              onChange={(e) => {
                sethours(e.target.value);
              }}
            />
          </div>
          <div class="col-span-4">
            <label for="pos-form-2" class="form-label">
            Killo Metre
            </label>
            <input
              id="pos-form-2"
              type="text"
              name="ending_km"
              class="form-control flex-1"
              placeholder=" Killo Metre"
              onChange={(e) => {
                setkilloMetre(e.target.value);
              }}
            />
          </div>
          </div>
        </div>

        <div class="modal-footer text-right">
          <Link
            to=""
            data-dismiss="modal"
            class="btn btn-outline-secondary w-32 mr-1"
          >
            Cancel
          </Link>
          
            <Link to='/Packages' class="btn btn-primary w-32" onClick={submitData}>Add</Link>
          
        </div>
      </div>
  );
};

export default DailyFareAdd;
