import React, { useState } from "react";
import TopBarSection from "../../TopBar/TopBarSection";
import { Link } from "react-router-dom";
import Axios from "axios";

const AddDriverVeh = () => {
  const [country, setCountry] = useState("");
  const [driverName, setDriverName] = useState("");
  const [vehicleType, setvehicleType] = useState("");
  const [brand, setbrand] = useState("");
  const [color, setcolor] = useState("");
  const [vehicleName, setvehicleName] = useState("");
  const [vehicleNumber, setvehicleNumber] = useState("");

  const submitData = () => {
    Axios.post("http://localhost:3001/api/insertdrivervehicles", {
      country: country,
      driver_name: driverName,
      vehicle_type: vehicleType,
      brand: brand,
      color: color,
      vehicle_name: vehicleName,
      vehicle_number: vehicleNumber,
    });
  };

  return (
    <div className="content">
      <TopBarSection />

      <div class="modal-content">
        <div class="modal-header">
          <h2 class="font-medium text-base mr-auto">Add New</h2>
        </div>
        <div class="modal-body grid grid-cols-12 gap-4 gap-y-3">
          <div class="col-span-6">
            <label for="pos-form-1" class="form-label">
              Country
            </label>
            <input
              id="pos-form-1"
              type="text"
              name="admin_fname"
              class="form-control flex-1"
              placeholder="Country"
              onChange={(e) => {
                setCountry(e.target.value);
              }}
            />
          </div>
          <div class="col-span-6">
            <label for="pos-form-2" class="form-label">
              Driver Name
            </label>
            <input
              id="pos-form-2"
              type="text"
              name="admin_lname"
              class="form-control flex-1"
              placeholder="Driver Name"
              onChange={(e) => {
                setDriverName(e.target.value);
              }}
            />
          </div>
          <div class="col-span-6">
            <label for="pos-form-2" class="form-label">
              Vehicle Type
            </label>
            <input
              id="pos-form-2"
              type="text"
              name="admin_username"
              class="form-control flex-1"
              placeholder="Vehicle Type"
              onChange={(e) => {
                setvehicleType(e.target.value);
              }}
            />
          </div>

          <div class="col-span-6">
            <label for="pos-form-2" class="form-label">
              Brand
            </label>
            <input
              id="pos-form-2"
              type="text"
              name="admin_password"
              class="form-control flex-1"
              placeholder="Brand"
              onChange={(e) => {
                setbrand(e.target.value);
              }}
            />
          </div>
          <div class="col-span-6">
            <label for="pos-form-2" class="form-label">
              Color
            </label>
            <input
              id="pos-form-2"
              type="text"
              name="admin_phone"
              class="form-control flex-1"
              placeholder="Color"
              onChange={(e) => {
                setcolor(e.target.value);
              }}
            />
          </div>
          <div class="col-span-6">
            <label for="pos-form-2" class="form-label">
              Vehicle Name
            </label>
            <input
              id="pos-form-2"
              type="email"
              name="admin_email"
              class="form-control flex-1"
              placeholder="Vehicle Name"
              onChange={(e) => {
                setvehicleName(e.target.value);
              }}
            />
          </div>
          <div class="col-span-6">
            <label for="pos-form-2" class="form-label">
              Vehicle Number
            </label>
            <input
              id="pos-form-2"
              type="text"
              name="admin_desc"
              class="form-control flex-1"
              placeholder="Vehicle Number"
              onChange={(e) => {
                setvehicleNumber(e.target.value);
              }}
            />
          </div>
        </div>

        <div class="modal-footer text-right">
          <Link
            to=""
            data-dismiss="modal"
            class="btn btn-outline-secondary w-32 mr-1"
          >
            Cancel
          </Link>
          <Link to="/driver-vehicle">
            <button class="btn btn-primary w-32" onClick={submitData}>Add</button>
          </Link>
        </div>
      </div>
    </div>
  );
};

export default AddDriverVeh;
