import React from 'react'
import '../../App.css';
import "../fontawesome/index";
import HomeScreen from '../screens/Home/HomeScreen';
import { BrowserRouter, Route, Routes } from 'react-router-dom';
import AmenitiesScreen from '../screens/Amenities/AmenitiesScreen';
import DashboardScreen from '../screens/Dashboard/DashboardScreen';
import CencellationSetting from '../screens/Settings/CancellationSetting/CencellationSetting';
import TripSetting from '../screens/Settings/TripSetting/TripSetting';
import ReferralSetting from '../screens/Settings/ReferralSetting/ReferralSetting';
import Appsetting from '../screens/Settings/AppSetting/Appsetting';
import ScratchCard from '../screens/Settings/ScratchCard/ScratchCard';
import FeatureSeting from '../screens/Settings/FeatureSetting/FeatureSeting';
import VehicleCategories from '../screens/VehicleCategories/VehicleCategories';
import Roles from '../screens/Roles/Roles';
import ManualBooking from '../screens/ManualBooking/ManualBooking';
import Navbar from '../screens/Navbar/Navbar';
import Customers from '../screens/Customers/Customers/CustomersScreen';
import { auth } from "../../firebase";
import CustomerSos from '../screens/Customers/CustomerSos/CustomerSos';
import CustomerWalletHistory from '../screens/Customers/CustomerWalletHistory/CustomerWalletHistory';
import DriverQueries from '../screens/Driver/DriverQueries/DriverQueries';
import DriverVeh from '../screens/Driver/DriverVeh/DriverVeh';
import DriverEarining from '../screens/Driver/DriverEarining/DriverEarining';
import DriverWithdrawls from '../screens/Driver/DriverWithdrawls/DriverWithdrawls';
import DailyFare from '../screens/FareManagement/DailyFareAdd/DailyFare';
import DailyFareMana from '../screens/FareManagement/DailyFareMana/DailyFareMana';
import OutstationFareMana from '../screens/FareManagement/OutstationFareMana/OutstationFareMana';
import Packages from '../screens/FareManagement/Packages/Packages';
import RentalFareMana from '../screens/FareManagement/RentalFareMana/RentalFareMana';
import PaymentTypes from '../screens/Payments/PaymentTypes/PaymentTypes';
import PaymentMethod from '../screens/Payments/PaymentMethods/PaymentMethod';
import PaymentHistory from '../screens/Payments/PaymentHistory/PaymentHistory';
import PaymentGatewaySetting from '../screens/Payments/PaymentGatewaySetting/PaymentGatewaySetting';
import BookingStatus from '../screens/BookingStatus/BookingStatus';
import Promocodes from '../screens/PromoCodes/Promocodes';
import TaxLists from '../screens/TaxLists/TaxLists';
import NotificationMessage from '../screens/NotificationMessage/NotificationMessage';
import CancellationReasons from '../screens/CancellationReasons/CancellationReasons';
import Messages from '../screens/Messages/Messages';
import AmenitiesAddData from '../screens/Amenities/AmenitiesAddData';
import Driverr from '../screens/Driver/Driver/Driver';
import DriverData from '../screens/Driver/Driver/DriverData';
import Addadmin from '../screens/Roles/Addadmin';
import DriverRecharge from '../screens/Driver/DriverRecaharge/DriverRecharge';
import DriverWalletHistory from '../screens/Driver/DriverWalletHistory/DriverWalletHistory';
import DriverBankDetail from '../screens/Driver/DriverBankDetail/DriverBankDetail';

import Trips from '../screens/Trips/Trips/Trips';
import TripRequest from '../screens/Trips/TripRequest/TripRequest';
import DriverTripRequest from '../screens/Trips/DriverTripRequest/DriverTripRequest';
import TripsType from '../screens/Trips/TripsType/TripsType';
import TripRequestStatus from '../screens/Trips/TripRequestStatus/TripRequestStatus';
import DefineRoles from '../screens/Roles/DefineRoles';
import Faqs from '../screens/Faqs/Faqs';
import PrivacyPolicies from '../screens/privacyPolcies/PrivacyPolicies';
import Currencies from '../screens/Currencies/Currencies';
import Countries from '../screens/Countries/Countries';
import AddRoles from '../screens/Roles/AddRoles';
import AfterAddingRoles from '../screens/Roles/AfterAddingRoles';
import PermissionScreen from '../screens/Roles/PermissionScreen';
import AddPermissions from '../screens/Roles/AddPermissions';
import VehicleCategoriesData from '../screens/VehicleCategories/VehicleCategoriesData';
import RentalBooking from '../screens/ManualBookingTwo/RentalBooking/RentalBooking';
import AddDriverVeh from '../screens/Driver/DriverVeh/AddDriverVeh';
import DailyFareAdd from '../screens/FareManagement/DailyFareAdd/DailyFareAdd';
import AddDailyFareMana from '../screens/FareManagement/DailyFareMana/AddDailyFareMana';
import AddOutstandingFareMana from '../screens/FareManagement/OutstationFareMana/AddOutstandingFareMana';
import AddPackages from '../screens/FareManagement/Packages/AddPackages';
import AddRentalFareMana from '../screens/FareManagement/RentalFareMana/AddRentalFareMana';
import ContactSetting from '../screens/Settings/ContactSetting/ContactSetting';
import AddContact from '../screens/Settings/ContactSetting/AddContact';


const Dashboard = () => {
  const logOutFUnction = async () => {
    try {
      await auth.signOut();
      localStorage.removeItem("tokenOtp");
      localStorage.removeItem("loggedInOtp");
      localStorage.removeItem("loggedIn");
      window.location.reload(false);
    } catch (error) {
      console.log("error signing out: ", error);
    }
  };
  return (

    <>

      <BrowserRouter>
        <div className="flex">
          <Navbar />
          <Routes>

            {/* <Route path="/Normal-Booking" element={<NormalBooking />} /> */}
            <Route path="/add-driver-veh" element={<AddDriverVeh />} />
            <Route path="/Rental-Booking" element={<RentalBooking />} />


            <Route path="/Add-Vehicle-Categories" element={<VehicleCategoriesData />} />
            <Route path="/Add-Permissions" element={<AddPermissions />} />
            <Route path="/All-Permissions" element={<PermissionScreen />} />
            <Route path="/All-Roles" element={<AfterAddingRoles />} />
            <Route path="/Add-Roles" element={<AddRoles />} />

            <Route path="/Currencies" element={<Currencies />} />

            <Route path="/Countries" element={<Countries />} />

            <Route path="/Privacy-Policies" element={<PrivacyPolicies />} />

            <Route path="/faqs" element={<Faqs />} />

            <Route path="/Trips" element={<Trips />} />
            <Route path="/Trips-Types" element={<TripsType />} />
            <Route path="/Trip-Request" element={<TripRequest />} />
            <Route path="/Driver-Trip-Request" element={<DriverTripRequest />} />
            <Route path="/Trip-Request-Status" element={<TripRequestStatus />} />

            <Route path="/Cancellation-Reasons" element={<CancellationReasons />} />
            <Route path="/Messages" element={<Messages/>} />
            <Route path="/Notification-Message" element={<NotificationMessage />} />
            <Route path="/Tax-Lists" element={<TaxLists/>} />
            <Route path="/Promo-codes" element={<Promocodes/>} />
            <Route path="/Booking-Status" element={<BookingStatus/>} />

            <Route path="/Payment-Gateway-Setting" element={<PaymentGatewaySetting/>} />
            <Route path="/Payment-History" element={<PaymentHistory/>} />
            <Route path="/Payment-Types" element={<PaymentTypes/>} />
            <Route path="/Payment-Method" element={<PaymentMethod/>} />
            <Route path="/Contact-Setting" element={<ContactSetting/>} />
            <Route path="/Add-Contact" element={<AddContact />} />

            <Route path="/Rental-Fare-Mana" element={<RentalFareMana/>} />
            <Route path="/Packages" element={<Packages/>} />
            <Route path="/Outstation-Fare-Mana" element={<OutstationFareMana/>} />
            <Route path="/Daily-Fare-Mana" element={<DailyFareMana/>} />
            <Route path="/Daily-Fare" element={<DailyFare/>} />
            <Route path="/Daily-Fare-Add" element={<DailyFareAdd/>} />
            <Route path="/add-daily-fare-mana" element={<AddDailyFareMana/>} />
            <Route path="/add-outstanding-fare-mana" element={<AddOutstandingFareMana/>} />
            <Route path="/add-packages" element={<AddPackages/>} />
            <Route path="/add-rental-fare-mana" element={<AddRentalFareMana/>} />


            <Route path="/Driver-Withdrawls" element={<DriverWithdrawls/>} />
            <Route path="/driver-queries" element={<DriverQueries/>} />
            <Route path="/driver-vehicle" element={<DriverVeh/>} />
            <Route path="/driver-earinings" element={<DriverEarining/>} />
            <Route path="/driver" element={<Driverr />} />
            <Route path="/Add-driver" element={<DriverData />} />

            <Route path="/Drivers-Recharges" element={<DriverRecharge />} />
            <Route path="/Drivers-Wallet-Histories" element={<DriverWalletHistory />} />
            <Route path="/Drivers-Bank-Details" element={<DriverBankDetail />} />
            {/* <Route path="/Drivers-Tutorials" element={< />} /> */}

            <Route path="/customer-wallet" element={<CustomerWalletHistory />} />
            <Route path="/customer-sos" element={<CustomerSos />} />
            <Route path="/customers" element={<Customers />} />

            <Route path="/manual-booking" element={<ManualBooking />} />
            <Route path="/vehicle-categories" element={<VehicleCategories />} />
            <Route path="/feature-setting" element={<FeatureSeting />} />
            <Route path="/scratch-card-setting" element={<ScratchCard />} />
            <Route path="/app-setting" element={<Appsetting />} />
            <Route path="/referral" element={<ReferralSetting />} />
            <Route path="/trip-setting" element={<TripSetting />} />
            <Route path="/cancellation-Setting" element={<CencellationSetting />} />
            <Route path="/dashboard" element={<DashboardScreen />} />

            <Route path="/Amenities-Add-Data" element={<AmenitiesAddData />} />
            <Route path="/amenities" element={<AmenitiesScreen />} />

            <Route path="/define-Roles" element={<DefineRoles />} />
            <Route path="/Add-Admin" element={<Addadmin />} />
            <Route path="/roles" element={<Roles />} />
            <Route path="/" element={<HomeScreen />} />

          </Routes>
        </div>

      </BrowserRouter>
      <div>
        <button style={{ marginLeft: "20px" }} onClick={logOutFUnction}>
          Logout
        </button>
      </div>
    </>
  )
}

export default Dashboard
