import React, { useState } from 'react'
import TopBarSection from '../TopBar/TopBarSection'
import Axios from 'axios';
import { Link } from 'react-router-dom';

const AddRoles = () => {
    const [role_name, setRole_name] = useState('')
    const [role_description, setRole_description] = useState('')

    const submitRolesData = () => {
        Axios.post('http://localhost:3001/api/insetRoles', {
            role_name: role_name,
            role_description: role_description,
        });
    }
    return (
        <div className="content">
            <TopBarSection />

            <div class="modal-content">
                <div class="modal-header">
                    <h2 class="font-medium text-base mr-auto">
                        Add New Role
                    </h2>
                </div>
                <div class="modal-body grid grid-cols-12 gap-4 gap-y-3">
                    <div class="col-span-12">
                        <label for="pos-form-1" class="form-label">Role Name</label>
                        <input id="pos-form-1" type="text" name="role_name" class="form-control flex-1" placeholder="Role Name" onChange={(e) => {
                            setRole_name(e.target.value)
                        }} />
                    </div>
                    <div class="col-span-12">
                        <label for="pos-form-1" class="form-label">Role Description</label>
                        <input id="pos-form-1" type="text" name="role_description" class="form-control flex-1" placeholder="Role Description" onChange={(e) => {
                            setRole_description(e.target.value)
                        }} />
                    </div>
                </div>
                <div class="modal-footer text-right">
                    <Link to="/All-Roles" type="button" data-dismiss="modal" class="btn btn-outline-secondary w-32 mr-1">Cancel</Link>
                    <Link to="/All-Roles" class="btn btn-primary w-32" onClick={submitRolesData}>Add Role</Link>
                </div>
            </div>
        </div>
    )
}

export default AddRoles
