import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import { ACTIONS, BaseFare, Country, Id, PricePerKm, STATUS, VehicleType } from '../../Components/TableHeading/TableHeading'
import TopBarSection from '../TopBar/TopBarSection'

const VehicleCategories = () => {

    const [vehicleCategoriesRoute, setVehicleCategoriesRoute] = useState([])
    useEffect(() => {
        fetch('http://localhost:3001/vehicleCategoriesRoute').then((res) => {
            console.log(res)
            return res.json()
        })
            .then((res) => {
                console.log('Success', res)
                setVehicleCategoriesRoute(res)
                return res
            })
            .catch((err) => {
                console.log(err)
            })
    }, [])
    return (
            <div className="content">
                <TopBarSection />

                <div className="col-span-12 mt-6">
                    <div className="intro-y block sm:flex items-center h-10">
                        <h2 className="text-lg font-medium truncate mr-5">
                        Vehicle Categories
                        </h2>
                        <div className="flex items-center sm:ml-auto mt-3 sm:mt-0">
                            <Link class="btn btn-primary shadow-md mr-2" to="/Add-Vehicle-Categories" >Add New</Link>
                            <button className="btn box flex items-center text-gray-700 dark:text-gray-300"> <i data-feather="file-text" className="hidden sm:block w-4 h-4 mr-2"></i> Export to Excel </button>
                            <button className="ml-3 btn box flex items-center text-gray-700 dark:text-gray-300"> <i data-feather="file-text" className="hidden sm:block w-4 h-4 mr-2"></i> Export to PDF </button>
                        </div>
                    </div>
                    <div className="intro-y overflow-auto lg:overflow-visible mt-8 sm:mt-0">
                        <table className="table table-report sm:mt-2">
                            <thead>
                                <tr>
                                    <th className="whitespace-nowrap">na</th>
                                   <Id />
                                   <Country />
                                   <VehicleType />
                                   <BaseFare />
                                   <PricePerKm />
                                   <STATUS />
                                   <ACTIONS />
                                </tr>
                            </thead>
                            <tbody>
                            {
                                vehicleCategoriesRoute.map(vehicleCategories => {
                                    return (
                                        <tr className="intro-x">
                                            <td className="text-center">NA</td>
                                            <td className="text-center">{vehicleCategories.vehicleCategories_id}</td>
                                            <td className="text-center">{vehicleCategories.vehicleCategories_country}</td>
                                            <td className="text-center">{vehicleCategories.vehicleCategories_type}</td>
                                            <td className="text-center">{vehicleCategories.vehicleCategories_base_fare}</td>
                                            <td className="text-center">{vehicleCategories.vehicleCategories_perkm}</td>
                                            <td className="w-40">
                                                <div className="flex items-center justify-center text-theme-9"> <i data-feather="check-square" className="w-4 h-4 mr-2"></i> Active </div>
                                            </td>
                                            <td className="table-report__action w-56">
                                                <div className="flex justify-center items-center">
                                                    <Link className="flex items-center mr-3" to=""> <i data-feather="check-square" className="w-4 h-4 mr-1"></i> Edit </Link>
                                                    <Link className="flex items-center text-theme-6" to=""> <i data-feather="trash-2" className="w-4 h-4 mr-1"></i> Delete </Link>
                                                </div>
                                            </td>
                                        </tr>
                                    )
                                })
                            }
                            </tbody>
                        </table>
                    </div>
                    <div className="intro-y flex flex-wrap sm:flex-row sm:flex-nowrap items-center mt-3">
                        <ul className="pagination">
                            <li>
                                <a className="pagination__link" href=""> <i className="w-4 h-4" data-feather="chevrons-left"></i> </a>
                            </li>
                            <li>
                                <a className="pagination__link" href=""> <i className="w-4 h-4" data-feather="chevron-left"></i> </a>
                            </li>
                            <li> <a className="pagination__link" href="">...</a> </li>
                            <li> <a className="pagination__link" href="">1</a> </li>
                            <li> <a className="pagination__link pagination__link--active" href="">2</a> </li>
                            <li> <a className="pagination__link" href="">3</a> </li>
                            <li> <a className="pagination__link" href="">...</a> </li>
                            <li>
                                <a className="pagination__link" href=""> <i className="w-4 h-4" data-feather="chevron-right"></i> </a>
                            </li>
                            <li>
                                <a className="pagination__link" href=""> <i className="w-4 h-4" data-feather="chevrons-right"></i> </a>
                            </li>
                        </ul>
                        <select className="w-20 form-select box mt-3 sm:mt-0">
                            <option>10</option>
                            <option>25</option>
                            <option>35</option>
                            <option>50</option>
                        </select>
                    </div>
                </div>
            </div>
    )
}

export default VehicleCategories
