import React, { useState } from 'react'
import Axios from 'axios';
import TopBarSection from '../TopBar/TopBarSection'
import { Link } from 'react-router-dom';

const VehicleCategoriesData = () => {

    const [vehicleCategories_country, setVehicleCategories_country] = useState('')
    const [vehicleCategories_type, setVehicleCategories_type] = useState('')
    const [vehicleCategories_base_fare, setVehicleCategories_base_fare] = useState('')
    const [vehicleCategories_perkm, setVehicleCategories_perkm] = useState('')

    const submitvehicleCategoriesData = () => {
        Axios.post('http://localhost:3001/api/insertvehicleCategories', {
            vehicleCategories_country: vehicleCategories_country,
            vehicleCategories_type: vehicleCategories_type,
            vehicleCategories_base_fare: vehicleCategories_base_fare,
            vehicleCategories_perkm: vehicleCategories_perkm,
        });

    }
    return (
        <div className="content">
            <TopBarSection />

            <div class="modal-content">
                <div class="modal-header">
                    <h2 class="font-medium text-base mr-auto">
                        New Vehicle Categories
                    </h2>
                </div>
                <div class="modal-body grid grid-cols-12 gap-4 gap-y-3">
                    <div class="col-span-12">
                        <label for="pos-form-1" class="form-label">country</label>
                        <input id="pos-form-1" type="text" name="vehicleCategories_country" class="form-control flex-1" placeholder="vehicle Categories country" onChange={(e) => {
                            setVehicleCategories_country(e.target.value)
                        }} />
                    </div>
                    <div class="col-span-12">
                        <label for="pos-form-2" class="form-label">vehicle type</label>
                        <input id="pos-form-2" type="text" name="vehicleCategories_type" class="form-control flex-1" placeholder="vehicle type" onChange={(e) => {
                            setVehicleCategories_type(e.target.value)
                        }} />
                    </div>
                    <div class="col-span-12">
                        <label for="pos-form-2" class="form-label">vehicle Base Fare</label>
                        <input id="pos-form-2" type="text" name="vehicleCategories_base_fare" class="form-control flex-1" placeholder="vehicle Base Fare" onChange={(e) => {
                            setVehicleCategories_base_fare(e.target.value)
                        }} />
                    </div>
                    <div class="col-span-12">
                        <label for="pos-form-2" class="form-label">Price Per km</label>
                        <input id="pos-form-2" type="text" name="vehicleCategories_perkm" class="form-control flex-1" placeholder="Price Per km" onChange={(e) => {
                            setVehicleCategories_perkm(e.target.value)
                        }} />
                    </div>
                </div>
                <div class="modal-footer text-right">
                    <button type="button" data-dismiss="modal" class="btn btn-outline-secondary w-32 mr-1">Cancel</button>
                    <Link class="btn btn-primary w-32" onClick={submitvehicleCategoriesData} to="/vehicle-categories">Add</Link>
                </div>
            </div>
        </div>
    )
}

export default VehicleCategoriesData
