import React, { useState } from "react";
import TopBarSection from "../../TopBar/TopBarSection";
import { Link } from "react-router-dom";
import Axios from "axios";

const DailyFareAdd = () => {
  const [country, setcountry] = useState("");
  const [vehicleType, setvehicleType] = useState("");
  const [packageId, setpackageId] = useState("");
  const [pricePerKm, setpricePerKm] = useState("");
  const [pricePerHour, setpricePerHour] = useState("");
  const [packageprice, setpackageprice] = useState("");
  

  const submitData = () => {
    Axios.post("http://localhost:3001/api/insertrentalfaremanat", {
        country: country,
        vehicleType: vehicleType,
        packageId: packageId,
        price_Per_Km: pricePerKm,
        price_per_Hour: pricePerHour,
        package_Price: packageprice,
    });
    console.log(submitData);
  };

  return (
    <div className="content">
      <TopBarSection />

      <div class="modal-content">
        <div class="modal-header">
          <h2 class="font-medium text-base mr-auto">Add New</h2>
        </div>
        <div class="modal-body grid grid-cols-12 gap-4 gap-y-3">
          <div class="col-span-6">
            <label for="pos-form-1" class="form-label">
              Country
            </label>
            <input
              id="pos-form-1"
              type="text"
              name="country"
              class="form-control flex-1"
              placeholder="Country"
              onChange={(e) => {
                setcountry(e.target.value);
              }}
            />
          </div>
          <div class="col-span-6">
            <label for="pos-form-2" class="form-label">
              Vehicle Type
            </label>
            <input
              id="pos-form-2"
              type="text"
              name="vehicleType"
              class="form-control flex-1"
              placeholder="Vehicle Type"
              onChange={(e) => {
                setvehicleType(e.target.value);
              }}
            />
          </div>
          <div class="col-span-6">
            <label for="pos-form-2" class="form-label">
            Package Id
            </label>
            <input
              id="pos-form-2"
              type="text"
              name="packageId"
              class="form-control flex-1"
              placeholder=" Package Id"
              onChange={(e) => {
                setpackageId(e.target.value);
              }}
            />
          </div>

          <div class="col-span-6">
            <label for="pos-form-2" class="form-label">
              Price Per Km
            </label>
            <input
              id="pos-form-2"
              type="text"
              name="price_Per_Km"
              class="form-control flex-1"
              placeholder="Price Per Km"
              onChange={(e) => {
                setpricePerKm(e.target.value);
              }}
            />
          </div>

          <div class="col-span-6">
            <label for="pos-form-2" class="form-label">
              Price Per Hour
            </label>
            <input
              id="pos-form-2"
              type="text"
              name="price_per_Hour"
              class="form-control flex-1"
              placeholder="Price Per Hour"
              onChange={(e) => {
                setpricePerHour(e.target.value);
              }}
            />
          </div>

          <div class="col-span-6">
            <label for="pos-form-2" class="form-label">
              Package Price
            </label>
            <input
              id="pos-form-2"
              type="text"
              name="packageprice"
              class="form-control flex-1"
              placeholder="Package Price"
              onChange={(e) => {
                setpackageprice(e.target.value);
              }}
            />
          </div>
          
          </div>
        </div>

        <div class="modal-footer text-right">
          <Link
            to=""
            data-dismiss="modal"
            class="btn btn-outline-secondary w-32 mr-1"
          >
            Cancel
          </Link>
          
            <Link to="/Rental-Fare-Mana" class="btn btn-primary w-32" onClick={submitData}>Add</Link>
          
        </div>
      </div>
  );
};

export default DailyFareAdd;
