import React, { useState,useEffect } from 'react'
import TopBarSection from '../TopBar/TopBarSection'
import Axios from 'axios';
import { Link } from 'react-router-dom';
// import {useHistory} from "react-router-dom";

const AddPermissions = () => {
    // const history = useHistory();
    const [permissions_name, setPermissions_name] = useState('')
    const [permissions_description, setPermissions_description] = useState('')
    const [role, setRole] = useState('')

    const submitRolesData = () => {
        Axios.post('http://localhost:3001/api/insetPermissions', {
            permissions_name: permissions_name,
            permissions_description: permissions_description,
            role: role,
        });
    }

    
    const [Roles, setRoles] = useState([])
    useEffect(() => {
        fetch('http://localhost:3001/Roles').then((res) => {
            console.log(res)
            return res.json()
        })
            .then((res) => {
                console.log('Success', res)
                setRoles(res)
                return res
            })
            .catch((err) => {
                console.log(err)
            })
    }, [])

    return (
        <div className="content">
            <TopBarSection />

            <div class="modal-content">
                <div class="modal-header">
                    <h2 class="font-medium text-base mr-auto">
                        Add New Permissions
                    </h2>
                </div>
                <div class="modal-body grid grid-cols-12 gap-4 gap-y-3">
                    <div class="col-span-12">
                        <label for="pos-form-1" class="form-label">Permissions Name</label>
                        <input id="pos-form-1" type="text" name="permissions_name" class="form-control flex-1" placeholder="Permissions Name" onChange={(e) => {
                            setPermissions_name(e.target.value)
                        }} />
                    </div>
                    <div class="col-span-12">
                        <label for="pos-form-1" class="form-label">Permissions Description</label>
                        <input id="pos-form-1" type="text" name="permissions_description" class="form-control flex-1" placeholder="Permissions Description" onChange={(e) => {
                            setPermissions_description(e.target.value)
                        }} />
                    </div>

                    <div class="col-span-12">
                        <label for="pos-form-2" class="form-label">Role</label>
                        <select name="role" class="form-control flex-1" onChange={(e) => {
                            setRole(e.target.value)
                        }}>
                            {
                                Roles.map(Role => {
                                    return (
                                        <option class="form-control flex-1" value={Role.role_name}>{Role.role_name}</option>
                                    )
                                })
                            }
                        </select>
                    </div>
                </div>
                <div class="modal-footer text-right">
                    <Link to="/All-Permissions" class="btn btn-outline-secondary w-32 mr-1" >Cancel</Link>
                    <Link to="/All-Permissions" class="btn btn-primary w-32" onClick={submitRolesData}>Add</Link>
                </div>
            </div>
        </div>
    )
}

export default AddPermissions
