import React, { useState } from "react";
import TopBarSection from "../../TopBar/TopBarSection";
import { Link } from "react-router-dom";
import Axios from "axios";

const DailyFareAdd = () => {
  const [vehicle, setvehicle] = useState("");
  const [startingkm, setstartingkm] = useState("");
  const [endingkm, setendingkm] = useState("");
  const [fare, setfare] = useState("");
  

  const submitData = () => {
    Axios.post("http://localhost:3001/api/insertdailyfareaddkm", {
        vehicles_type: vehicle,
        starting_km: startingkm,
        ending_km: endingkm,
        fare: fare,
    });
    console.log(submitData);
  };

  return (
    <div className="content">
      <TopBarSection />

      <div class="modal-content">
        <div class="modal-header">
          <h2 class="font-medium text-base mr-auto">Add New</h2>
        </div>
        <div class="modal-body grid grid-cols-12 gap-4 gap-y-3">
          <div class="col-span-6">
            <label for="pos-form-1" class="form-label">
              Vehicle
            </label>
            <input
              id="pos-form-1"
              type="text"
              name="vehicles_type"
              class="form-control flex-1"
              placeholder="Vehicle"
              onChange={(e) => {
                setvehicle(e.target.value);
              }}
            />
          </div>
          <div class="col-span-6">
            <label for="pos-form-2" class="form-label">
              Starting Km
            </label>
            <input
              id="pos-form-2"
              type="text"
              name="starting_km"
              class="form-control flex-1"
              placeholder="Satarting Km"
              onChange={(e) => {
                setstartingkm(e.target.value);
              }}
            />
          </div>
          <div class="col-span-6">
            <label for="pos-form-2" class="form-label">
            Ending Km 
            </label>
            <input
              id="pos-form-2"
              type="text"
              name="ending_km"
              class="form-control flex-1"
              placeholder="Ending Km"
              onChange={(e) => {
                setendingkm(e.target.value);
              }}
            />
          </div>

          <div class="col-span-6">
            <label for="pos-form-2" class="form-label">
              Fare
            </label>
            <input
              id="pos-form-2"
              type="text"
              name="fare"
              class="form-control flex-1"
              placeholder="Fare"
              onChange={(e) => {
                setfare(e.target.value);
              }}
            />
          </div>
          
          </div>
        </div>

        <div class="modal-footer text-right">
          <Link
            to=""
            data-dismiss="modal"
            class="btn btn-outline-secondary w-32 mr-1"
          >
            Cancel
          </Link>
          
            <Link to="/Daily-Fare" class="btn btn-primary w-32" onClick={submitData}>Add</Link>
          
        </div>
      </div>
  );
};

export default DailyFareAdd;
