import React from 'react'
import { PaymentHistor } from '../../../Components/Heading.js/Heading'
import { ACTIONS, Amount, Id, Mode, TripId } from '../../../Components/TableHeading/TableHeading'
import TopBarSection from '../../TopBar/TopBarSection'

const PaymentHistory = () => {
    return (
        <div className="content">
        <TopBarSection />

        <div className="col-span-12 mt-6">
            <div className="intro-y block sm:flex items-center h-10">
                <PaymentHistor />
                <div className="flex items-center sm:ml-auto mt-3 sm:mt-0">
                    <button className="btn box flex items-center text-gray-700 dark:text-gray-300"> <i data-feather="file-text" className="hidden sm:block w-4 h-4 mr-2"></i> Export to Excel </button>
                    <button className="ml-3 btn box flex items-center text-gray-700 dark:text-gray-300"> <i data-feather="file-text" className="hidden sm:block w-4 h-4 mr-2"></i> Export to PDF </button>
                </div>
            </div>

            <div className="intro-y overflow-auto lg:overflow-visible mt-8 sm:mt-0">
                <table className="table table-report sm:mt-2">
                    <thead>
                        <tr>
                            <Id />
                            <TripId />
                            <Mode />
                            <Amount />
                            <ACTIONS />
                        </tr>
                    </thead>
                    <tbody>

                    </tbody>
                </table>
            </div>
        </div>
    </div>
    )
}

export default PaymentHistory
