import React, { useState } from 'react'
import Axios from 'axios';
import TopBarSection from '../TopBar/TopBarSection'

const AmenitiesAddData = () => {
    const [name, setName] = useState('')
    const [icons, setIcons] = useState('')

    const submitData = () => {
        Axios.post('http://localhost:3001/api/insertamenities', {
            name: name,
            icon: icons,
        });

    }
    return (
        <div className="content">
            <TopBarSection />

            <div class="modal-content">
                <div class="modal-header">
                    <h2 class="font-medium text-base mr-auto">
                        New Order
                    </h2>
                </div>
                <div class="modal-body grid grid-cols-12 gap-4 gap-y-3">
                    <div class="col-span-12">
                        <label for="pos-form-1" class="form-label">Name</label>
                        <input id="pos-form-1" type="text" name="name" class="form-control flex-1" placeholder="name" onChange={(e) => {
                            setName(e.target.value)
                        }} />
                    </div>
                    <div class="col-span-12">
                        <label for="pos-form-2" class="form-label">icons</label>
                        <input id="pos-form-2" type="text" name="icon" class="form-control flex-1" placeholder="icons" onChange={(e) => {
                            setIcons(e.target.value)
                        }} />
                    </div>
                </div>
                <div class="modal-footer text-right">
                    <button type="button" data-dismiss="modal" class="btn btn-outline-secondary w-32 mr-1">Cancel</button>
                    <button class="btn btn-primary w-32" onClick={submitData}>Add Amenities</button>
                </div>
            </div>
        </div>
    )
}

export default AmenitiesAddData
