import React from 'react'
import TopBarSection from '../../TopBar/TopBarSection'

const DriverData = () => {

    const submitData = () => {
        // Axios.post('http://localhost:3001/api/insertamenities', {
        //     driver_name: driver_name,
        //     driver_email: driver_email,
        //     driver_password: driver_password,
        //     license: license,
        //     license_expire: license_expire,
        //     rc: rc,
        //     rc_expire: rc_expire,
        //     insurance: insurance,
        //     insurance_expire: insurance_expire,
        //     other_docs: other_docs,
        // });
    }
    return (
        <div className="content">
            <TopBarSection />

            <div class="modal-content">
                <div class="modal-header">
                    <h2 class="font-medium text-base mr-auto">
                        {/* New Order */}
                    </h2>
                </div>
                <div class="modal-body grid grid-cols-12 gap-4 gap-y-3">
                    <div class="col-span-6">
                        <label for="pos-form-1" class="form-label">Name</label>
                        <input id="pos-form-1" type="text" name="driver_name" class="form-control flex-1" placeholder="name" />
                    </div>
                    <div class="col-span-6">
                        <label for="pos-form-2" class="form-label">Phone</label>
                        <input id="pos-form-2" type="text" name="driver_phone" class="form-control flex-1" placeholder="Phone" />
                    </div>
                    <div class="col-span-6">
                        <label for="pos-form-2" class="form-label">Email</label>
                        <input id="pos-form-2" type="email" name="driver_email" class="form-control flex-1" placeholder="Email" />
                    </div>
                    <div class="col-span-6">
                        <label for="pos-form-2" class="form-label">Password</label>
                        <input id="pos-form-2" type="email" name="driver_password" class="form-control flex-1" placeholder="Password" />
                    </div>
                    <div class="col-span-6">
                        <label for="pos-form-2" class="form-label">License</label>
                        <input id="pos-form-2" type="email" name="license" class="form-control flex-1" placeholder="license" />
                    </div>
                    <div class="col-span-6">
                        <label for="pos-form-2" class="form-label">License Expire</label>
                        <input id="pos-form-2" type="email" name="license_expire" class="form-control flex-1" placeholder="license_expire" />
                    </div>
                    <div class="col-span-6">
                        <label for="pos-form-2" class="form-label">RC</label>
                        <input id="pos-form-2" type="email" name="rc" class="form-control flex-1" placeholder="rc" />
                    </div>
                    <div class="col-span-6">
                        <label for="pos-form-2" class="form-label">RC Expire</label>
                        <input id="pos-form-2" type="email" name="rc_expire" class="form-control flex-1" placeholder="rc_expire" />
                    </div>
                    <div class="col-span-6">
                        <label for="pos-form-2" class="form-label">Insurance</label>
                        <input id="pos-form-2" type="email" name="insurance" class="form-control flex-1" placeholder="insurance" />
                    </div>
                    <div class="col-span-6">
                        <label for="pos-form-2" class="form-label">Insurance Expire</label>
                        <input id="pos-form-2" type="email" name="insurance_expire" class="form-control flex-1" placeholder="insurance_expire" />
                    </div>
                    <div class="col-span-6">
                        <label for="pos-form-2" class="form-label">Other Docse</label>
                        <input id="pos-form-2" type="email" name="other_docs" class="form-control flex-1" placeholder="other_docs" />
                    </div>
                </div>
                <div class="modal-footer text-right">
                    <button type="button" data-dismiss="modal" class="btn btn-outline-secondary w-32 mr-1">Cancel</button>
                    <button class="btn btn-primary w-32" onClick={submitData}>Add Amenities</button>
                </div>
            </div>
        </div>
    )
}

export default DriverData
